.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    height: 96vh;
    width: 70vw;
    background: #0a6bf0;
 
}

header {
    margin-bottom: 15%;
}

.App__header {
    color: rgba(255, 255, 255, 0.91);
    font-size: 1.8rem;
}

.App__subheader {
    color: #b4c0d8;
    font-size: 0.7rem;
    font-weight: bold;
}

.App__text--white {
    color: #fff;
}

.Counter {
    margin: 0 auto 0 auto;
}

.Counter--main {
    background: #030303;
    -webkit-box-shadow: -12px 36px 54px 18px rgba(34, 39, 53, 0.43);
            box-shadow: -12px 36px 54px 18px rgba(34, 39, 53, 0.43);
    padding: 1.5rem 3.5rem 3rem;
    margin: 1.5rem 0;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.Counter--main__session {
    margin-left: auto;
}

.Counter--main__session>select {
    background: transparent;
    outline: 0;
    border: 0;
}

.Counter--main__values {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Counter--main__values>div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.Counter__separator {
    color: #fff;
    margin: 0 2rem;
}

.Counter__text--grey {
    color: #858ca6;
    font-size: 0.6rem;
}

.Counter__text--large {
    font-size: 3rem;
}

.App__buttons {
    text-align: center;
}

.App__buttons>button {
    background: transparent;
    outline: 0;
    border: 0;
    margin: 0 1rem;
    cursor: pointer;
}

.App__buttons>button:active {
    outline: 1px solid #fff;
}


